.App {
  text-align: center;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

/* NavBar Styling */
.nav-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 38px;
  font-family: 'Titillium Web', sans-serif;
  font-size: 2.0em;
}
.left-side {
  display: flex;
}
.right-side {
  display: flex;
  justify-content: space-between;
}

.nav-link-wrapper {
  margin-right: 20px;
  border-bottom: 1px solid transparent;
  transition: border-bottom 0.5s;
}

.nav-link-wrapper a {
  color: #8a8a8a;
  text-decoration: none;
  transition: color 0.5s;
}

.nav-link-wrapper:hover {
  border-bottom: 1px solid black;
}
.nav-link-wrapper a:hover {
  color: black;
}

.active-nav-link {
  border-bottom: 1px solid black;
}

.active-nav-link a {
  color: black !important;
}

.social-icon {
  padding-right: 30px;
  color: black
}

/*Portfolio styles*/
.portfolio-items-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.portfolio-item-wrapper {
  position: relative;
  padding: 19px;
  cursor: pointer;
}
.portfolio-panels {
  height: 500px;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.resume-panel_home {
  background-image: url(./images/work-area.jpg);
}
.play-panel_home {
  background-image: url(./images/film-stock-photo.jpg);
}
.project-panel_home {
  background-image: url(./images/project-pic.jpg);
}
.img-text-wrapper {
  position: absolute;
  top: 0;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width:100%;
}
.img-text-wrapper .subtitle {
  transition: 0.5s;
  color: transparent;
  font-size: 2.0em;
  font-weight: 600;
  font-family: 'Titillium Web', sans-serif;
  margin-left: 15%;
  margin-right: 10%;
}
.img-text-wrapper:hover .subtitle {
  color: white;
}
.img-darken {
  transition: 0.5s;
  filter: brightness(50%);
}

/* Project styles */
.project-panels-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: #F3F5FF;
}
.project-panel-wrapper {
  position: relative;
  padding: 19px;
  cursor: pointer;
}
.project-panel {
  height: 500px;
  width: 100%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.project-panel-description-title {
  font-weight: 'bold';
  display: 'flex';
  justify-content: 'center';
}
.project-panel-description {
  font-size: 2.0em; 
  font-family: 'Titillium Web', sans-serif;
  display: flex;
  flex-flow: column;
  justify-content: center;
  padding: 0px 25px;
}
.mobile-flip-div {
  display: contents;
}
.image-gallery-slide {
  width: 100%;
  max-height: 450px;
}
.image-gallery-slide img {
  width: 100%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.image-gallery-bullet {
  background: white !important;
}
.image-gallery-bullets .image-gallery-bullet.active {
  background: #48C9B0 !important;
}
.image-gallery-icon {
  color: #48C9B0 !important;
}

