/*About styles */
.aboutContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  margin: auto;
}
.aboutHeader {
  font-size: 1.5em; 
  font-family: 'Titillium Web', sans-serif;
  text-decoration: underline;
  padding-bottom: 15px;
}
.aboutBody {
  font-size: 1.0em; 
  font-family: 'Titillium Web', sans-serif;
}

/*Footer styles*/
footer {
  padding-top: 10px;
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  font-size: 1.5em; 
  font-family: 'Titillium Web', sans-serif;
}
.footer-image {
  width: 10%;
}
#fullname {
  display: initial;
}

@media only screen and (max-device-width: 1024px) {
  .img-text-wrapper .subtitle {
    transition: 0.5s;
    color: transparent;
    font-size: 1.5em;
    font-weight: 600;
    font-family: 'Titillium Web', sans-serif;
    margin-left: 15%;
  }
  /* NavBar Styling */
  .nav-wrapper {
    display: flex;
    justify-content: space-between;
    padding: 12px;
    font-family: 'Titillium Web', sans-serif;
    font-size: 1.0em;
  }
  .nav-link-wrapper:hover {
    border-bottom: 1px white;
  }
  .nav-link-wrapper a:hover {
    color: black;
  }
  .nav-link-wrapper {
    margin-right: 10px;
  }
  .active-nav-link {
    border-bottom: 1px white;
  }
  
  .active-nav-link a {
    color: white !important;
  }
  #project-link #fullname {
    display: none;
  }

  /* Project styles */
  .portfolio-items-wrapper {
    display: grid;
    grid-template-columns: 1fr;
  }
  /*Gallery style*/
  .play-items-wrapper {
    display: grid;
    grid-template-columns: 1fr;
  }
  /*Cinema style*/
  .music-items-wrapper {
    display: grid;
    grid-template-columns: 1fr;
  }
  /*Cinema style*/
  .cinema-items-wrapper {
    display: grid;
    grid-template-columns: 1fr;
  }
  /*About styles */
  .aboutContainer {
    display: inherit;
    justify-content: center;
    align-items: center;
    width: 70%;
    margin: auto;
  }
  /* Project styles */
  .project-panels-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    background-color: #F3F5FF;
  }
  .project-panel-description-title {
    font-weight: 'bold';
    display: 'flex';
    justify-content: 'center';
    font-size: 0.9em;
    font-family: 'Titillium Web', sans-serif;
  }
  .project-specs {
    display: none;
  }
  .mobile-flip-div {
    display: flex;
    flex-direction: column-reverse;
  }
}
@media only screen and (max-device-width: 420px) {
  #fullname {
    display: none;
  }
}